// functions/api/sidebar.ts
type Ctx = { DB: D1Database; LOCAL_DEV_MODE: string };

async function counts(db: D1Database) {
  // adjust names to your schema; all IF EXISTS-safe on empty DBs
  const q = async (sql: string) =>
    Number((await db.prepare(sql).first())?.n ?? 0);

  return {
    companies:  await q("SELECT COUNT(*) n FROM Companies"),
    locations:  await q("SELECT COUNT(*) n FROM Locations"),
    fleets:     await q("SELECT COUNT(*) n FROM Fleets"),
    assets:     await q("SELECT COUNT(*) n FROM Assets"),
    variables:  await q("SELECT COUNT(*) n FROM Variables"),
    oracles:    await q("SELECT COUNT(*) n FROM Oracles"),
    dashboards: await q("SELECT COUNT(*) n FROM Dashboards"), // if you have this table
  };
}

export const onRequestGet: PagesFunction<Ctx> = async (ctx) => {
  const dev = ctx.env.LOCAL_DEV_MODE === "1";
  const uid = dev ? (ctx.request.headers.get("x-dev-uid") ?? "dev")
                  : null; // TODO: verify JWT in prod and set UID

  const c = await counts(ctx.env.DB);

  // Build the sidebar model your UI expects
  const resp = {
    user: { uid },
    sections: [
      {
        key: "your",
        label: "Your Network",
        groups: [
          {
            key: "data",
            label: "Data",
            items: [
              { key: "variables", label: "Variables", count: c.variables, icon: "Sliders" },
              { key: "dashboards", label: "Dashboards", count: c.dashboards ?? 0, icon: "LayoutDashboard" },
              { key: "explorer", label: "Explorer", count: null, icon: "Compass" },
              { key: "download", label: "Download", count: null, icon: "Download" },
            ],
          },
          {
            key: "hardware",
            label: "Hardware",
            items: [
              { key: "overview", label: "Overview", count: null, icon: "Cpu" },
              { key: "oracles", label: "Oracles", count: c.oracles, icon: "Antenna" },
              { key: "loggers", label: "Loggers", count: null, icon: "Radio" }, // fill later if you have a table
            ],
          },
          {
            key: "hierarchy",
            label: "Hierarchy",
            items: [
              { key: "companies", label: "Companies", count: c.companies, icon: "Building2" },
              { key: "locations", label: "Locations", count: c.locations, icon: "MapPin" },
              { key: "fleets", label: "Fleets", count: c.fleets, icon: "Truck", disabled: c.fleets === 0 }, // example gating
              { key: "assets", label: "Assets", count: c.assets, icon: "Boxes" },
              { key: "variables_h", label: "Variables", count: c.variables, icon: "Sliders" },
            ],
          },
        ],
      },
      {
        key: "public",
        label: "Public Network",
        groups: [
          {
            key: "data_pub",
            label: "Data",
            items: [
              { key: "variables_pub", label: "Variables", count: c.variables, icon: "Sliders" },
              { key: "dashboards_pub", label: "Dashboards", count: c.dashboards ?? 0, icon: "LayoutDashboard" },
            ],
          },
        ],
      },
    ],
  };

  return Response.json(resp);
};
