export const onRequestGet: PagesFunction = async (ctx) => {
  // trust dev header in local mode
  const dev = ctx.env.LOCAL_DEV_MODE === "1";
  const uid = dev ? (ctx.request.headers.get("x-dev-uid") ?? "dev") : "prod-user";
  // TODO: add D1 lookups for dashboards by UID
  return Response.json({
    user: { sub: uid, email: `${uid}@example.dev` },
    dashboards: [],
  });
};

